import { yupResolver } from '@hookform/resolvers/yup'
import { Stack, TextField } from '@mui/material'
import { MailingApi } from 'API/mailing'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	email: string
}

export default function AddMail() {
	const nav = useNavigate()
	const { id } = useParams<{ id: string }>()
	const mailing = useAppSelector((s) => s.Mailing.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	const dispatch = useAppDispatch()

	const schema = yup.object({
		email: yup.string().required().email(),
	})
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			email: '',
		},
	})
	useEffect(() => {
		return () => {
			setOpen(false)
		}
	}, [])
	if (!mailing || !id) return null

	const onSubmit: SubmitHandler<FormInput> = async ({ email }) => {
		try {
			setLoading(true)
			const { error, message } = await MailingApi.addMember({
				email,
				id,
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			if (!error) nav('/mailing/' + id)
		} finally {
			setLoading(false)
		}
	}
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => {
				nav('/mailing/' + id)
			}}
			title="メールアドレス追加"
			disabled={Object.keys(errors).length > 0}
			primaryButtonText="登録"
			primaryButtonColor="success"
			component="form"
			maxWidth="sm"
			handleSubmit={handleSubmit(onSubmit)}
		>
			<Stack gap={1}>
				<TextField
					variant="standard"
					autoFocus
					required
					margin="dense"
					fullWidth
					size="small"
					label={'メールアドレス'}
					placeholder="ex:xxx@caa.go.jp"
					InputLabelProps={{ shrink: true }}
					{...register('email')}
					error={'email' in errors}
				/>
			</Stack>
		</DialogConfirm>
	)
}
